import {Navbar,Container} from 'react-bootstrap';
const logo = require("../assets/headset.png")

function NavBar() {
    return (
        <>
            <Navbar bg="dark" variant="dark">
                <Container>
                    <Navbar.Brand href="#home">
                        <img
                            alt=""
                            src={logo}
                            width="30"
                            height="30"
                            className="d-inline-block align-top"
                        />{'\t'}
                        MvMaker Demo
                    </Navbar.Brand>
                </Container>
            </Navbar>
        </>
    );
}

export default NavBar;