import React, {Component} from "react";
import {Button, Figure, Form, Spinner} from "react-bootstrap";
import {Field, Formik} from "formik";
import * as yup from "yup";
import "./demo.scss"
import axios from "axios";
import MusicButton from "./MusicButton";


class DemoPage extends Component {
    state = {
        latents: ["default", "kelp", "tauceti"],
        noises: ["default", "circular mask"],
        bends: ["default", "zoom"],
        show: false,
        values: {},
        ckpt: {},
        requestMade: false,
        selectedNetwork: false,
        selectedMusic: false,
        videoURL: "",
        videoURLRes: 0,
        networks: [
            {
                ckpt: "afhqcat",
                G_res: 512,
                out_size: 512
            }, {
                ckpt: "afhqdog",
                G_res: 512,
                out_size: 512
            }, {
                ckpt: "afhqwild",
                G_res: 512,
                out_size: 512
            },
            {
                ckpt: "brecahad",
                G_res: 512,
                out_size: 512
            },
            {
                ckpt: "ffhq",
                G_res: 1024,
                out_size: 1024
            },
        ],
        songs: ["trap.wav", "rock.wav", "dubstep.wav", "classical.wav", "pop.wav"]

    };

    toggleEditorDialog = () => {
        this.setState({show: !this.state.show})
    }


    handleSubmit = (values, {resetForm}) => {
        values.ckpt = {...this.state.ckpt};
        console.log(values)
        this.setState({requestMade: true})
        let ckpt = this.state.ckpt;
        let request = {
            video_name: "DEMO-" + Date.now(),
            network: ckpt.ckpt,
            G_res: ckpt.G_res,
            out_size: ckpt.out_size,
            latent: values.latent,
            noise: values.noise,
            bend: values.bend,
            song: values.song,
            selectedNetwork: false,
            selectedMusic: false,
        }
        this.setState({videoURLRes: ckpt.G_res})


        axios.post("http://localhost:8001/gen_video", request)
            .then((response) => {
                console.log(response.data)
                this.setState({videoURL: response.data})
            })
            .then(() => {
                this.setState({
                    latents: ["default", "kelp", "tauceti"],
                    noises: ["default", "circular mask"],
                    bends: ["default", "zoom"],
                    show: false,
                    selectedNetwork: false,
                    selectedMusic: false,
                    values: {},
                    ckpt: {},
                    networks: [
                        {
                            ckpt: "afhqcat",
                            G_res: 512,
                            out_size: 512
                        },
                        {
                            ckpt: "brecahad",
                            G_res: 512,
                            out_size: 512
                        },
                        {
                            ckpt: "ffhq",
                            G_res: 1024,
                            out_size: 1024
                        },
                    ],
                    songs: ["trap.wav", "rock.wav", "dubstep.wav", "classical.wav", "pop.wav"]
                })
                resetForm();
            })

    };


    render() {

        return (
            <div>
                <Formik
                    enableReinitialize={true}
                    initialValues={{

                        ckpt: '{"ckpt": "","G_res": 0,"out_size": 0}',
                        G_res: 0,
                        out_size: 0,
                        song: "",
                        has_latent: false,
                        has_noise: false,
                        has_bends: false,
                        latent: "default",
                        noise: "default",
                        bend: "default",
                        power: 0,
                        smoothness: 0,
                        freq_min: 0,
                        freq_max: 0,
                    }}
                    validationSchema={myFormSchema}
                    onSubmit={this.handleSubmit}
                >
                    {({
                          values,
                          errors,
                          handleChange,
                          handleBlur,
                          handleSubmit,
                          isSubmitting
                          /* and other goodies */
                      }) => (
                        <div className="card mb-4">
                            <form onSubmit={handleSubmit} className="position-relative">
                                <div className="card-body">
                                    <h3>{`Choose ${this.state.selectedNetwork === false ? 'a network' : this.state.selectedMusic === false ? 'a song' : 'options'}`}</h3>
                                    {this.state.selectedNetwork === false ? <>
                                            <div className={"row m-4"}>
                                                <div className={"col col-md-4"}>
                                                    <div className="container cursor-pointer">
                                                        <img onClick={(e) => {
                                                            this.setState({
                                                                ckpt: {
                                                                    ckpt: "afhqcat",
                                                                    G_res: 512,
                                                                    out_size: 512
                                                                }
                                                            })
                                                            this.setState({selectedNetwork: true})
                                                        }} src={require("./assets/cat.png")} alt="Avatar"
                                                             className="image"
                                                             style={{width: "75%", height: "auto"}}/>
                                                        <div className="middle">
                                                            <div className="text">AFHQCAT</div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className={"col col-md-4"}>
                                                    <div className="container cursor-pointer">
                                                        <img onClick={(e) => {
                                                            this.setState({
                                                                ckpt: {
                                                                    ckpt: "afhqdog",
                                                                    G_res: 512,
                                                                    out_size: 512
                                                                }
                                                            })
                                                            this.setState({selectedNetwork: true})
                                                        }} src={require("./assets/dog.png")} alt="Avatar" className="image"
                                                             style={{width: "75%", height: "auto"}}/>
                                                        <div className="middle">
                                                            <div className="text">AFHQDOG</div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className={"col col-md-4"}>
                                                    <div className="container">
                                                        <img src={require("./assets/afhqwild.png")} alt="Avatar"
                                                             className="image"
                                                             style={{width: "75%", height: "auto"}}
                                                             onClick={(e) => {
                                                                 this.setState({
                                                                     ckpt: {
                                                                         ckpt: "afhqwild",
                                                                         G_res: 512,
                                                                         out_size: 512
                                                                     }
                                                                 })
                                                                 this.setState({selectedNetwork: true})
                                                             }}
                                                        />
                                                        <div className="middle">
                                                            <div className="text">AFHQWILD</div>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                            <div className={"row m-4 mt-6"}>

                                                <div className={"col col-md-4"}>
                                                    <div className="container">
                                                        <img src={require("./assets/metfaces.png")} alt="Avatar"
                                                             className="image"
                                                             style={{width: "75%", height: "auto"}}
                                                             onClick={(e) => {
                                                                 this.setState({
                                                                     ckpt: {
                                                                         ckpt: "metfaces",
                                                                         G_res: 1024,
                                                                         out_size: 1024
                                                                     }
                                                                 })
                                                                 this.setState({selectedNetwork: true})
                                                             }}
                                                        />
                                                        <div className="middle">
                                                            <div className="text">METFACES</div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className={"col col-md-4"}>
                                                    <div className="container">
                                                        <img src={require("./assets/brecahad.png")} alt="Avatar"
                                                             className="image"
                                                             style={{width: "75%", height: "auto"}}
                                                             onClick={(e) => {
                                                                 this.setState({
                                                                     ckpt: {
                                                                         ckpt: "brecahad",
                                                                         G_res: 512,
                                                                         out_size: 512
                                                                     }
                                                                 })
                                                                 this.setState({selectedNetwork: true})
                                                             }}
                                                        />
                                                        <div className="middle">
                                                            <div className="text">BRECAHAD</div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className={"col col-md-4"}>
                                                    <div className="container">
                                                        <img src={require("./assets/ffhq.png")} alt="Avatar"
                                                             className="image"
                                                             style={{width: "75%", height: "auto"}}
                                                             onClick={(e) => {
                                                                 this.setState({
                                                                     ckpt: {
                                                                         ckpt: "ffhq",
                                                                         G_res: 1024,
                                                                         out_size: 1024
                                                                     }
                                                                 })
                                                                 this.setState({selectedNetwork: true})
                                                             }}
                                                        />
                                                        <div className="middle">
                                                            <div className="text">FFHQ</div>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                        </> :
                                        <div className={"form-row row"}>
                                            {/*SONG*/}
                                            {this.state.selectedMusic === false ?
                                                <>
                                                    <div className="form-group col-md-4">
                                                        <Form.Label
                                                            className="ul-form__label"
                                                        >
                                                            Song:
                                                        </Form.Label>
                                                        <Field
                                                            className={"cursor-pointer form form-control row ml-0"}
                                                            component="select"
                                                            id="location"
                                                            name="song"
                                                            multiple={false}
                                                        >
                                                            <option key={"-1"}
                                                                    value={null}
                                                            >SELECT
                                                            </option>
                                                            {this.state.songs.map((song, id) => (
                                                                <option key={id}
                                                                        value={song}
                                                                >{song}</option>
                                                            ))}
                                                        </Field>
                                                        <small
                                                            id="passwordHelpBlock"
                                                            className={errors.song ? "ul-form__text form-text text-danger font-weight-bold" : "ul-form__text form-text"}
                                                        >
                                                            Please select a song
                                                        </small>
                                                    </div>
                                                    <div className="form-group col-md-4">
                                                        <Form.Label
                                                            className="ul-form__label"
                                                        />
                                                        {values.song && values.song.length > 0 ? <MusicButton music={values.song}/>:<></>}
                                                    </div>
                                                </> : <>
                                                    <div className="form-group col-md-3 ">
                                                        <Form.Label
                                                            className="ul-form__label"
                                                        >
                                                            Apply transformations:
                                                        </Form.Label>
                                                        <ul>
                                                            <li>
                                                                <label>
                                                                    <Field type="checkbox" name="has_latent"/>
                                                                    Latents
                                                                </label>
                                                            </li>
                                                            <br/>
                                                            <br/>
                                                            <li>
                                                                <label>
                                                                    <Field type="checkbox" name="has_noise"/>
                                                                    Noise
                                                                </label>
                                                            </li>
                                                            <br/>
                                                            <br/>
                                                            <li>
                                                                <label>
                                                                    <Field type="checkbox" name="has_bends"/>
                                                                    Network bending
                                                                </label>
                                                            </li>
                                                        </ul>
                                                    </div>
                                                    <div className="form-group col-md-5 ">
                                                        <br/>
                                                        <div className="form-group row mt-1 pb-2">
                                                            <Field
                                                                className={"cursor-pointer form form-control row ml-0"}
                                                                component="select"
                                                                id="location_1"
                                                                name="latent"
                                                                disabled={!values.has_latent}
                                                                multiple={false}
                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                            >
                                                                {this.state.latents.map((option, id) => (
                                                                    <option key={id}
                                                                            value={option}
                                                                    >{option}</option>
                                                                ))}
                                                            </Field>

                                                        </div>

                                                        <div className="form-group row mt-lg-4 pb-2">
                                                            <Field
                                                                className={"cursor-pointer form form-control row ml-0"}
                                                                component="select"
                                                                id="location_2"
                                                                name="noise"
                                                                disabled={!values.has_noise}
                                                                multiple={false}
                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                            >
                                                                {this.state.noises.map((option, id) => (
                                                                    <option key={id}
                                                                            value={option}
                                                                    >{option}</option>
                                                                ))}
                                                            </Field>

                                                        </div>
                                                        <div className="form-group row mt-lg-4 pb-2">
                                                            <Field
                                                                className={"cursor-pointer form form-control row ml-0"}
                                                                component="select"
                                                                id="location_2"
                                                                name="bend"
                                                                disabled={!values.has_bends}
                                                                multiple={false}
                                                                onChange={handleChange}
                                                                onBlur={handleBlur}
                                                            >
                                                                {this.state.bends.map((option, id) => (
                                                                    <option key={id}
                                                                            value={option}
                                                                    >{option}</option>
                                                                ))}
                                                            </Field>
                                                        </div>
                                                    </div>
                                                </>
                                            }
                                        </div>}

                                    {
                                        this.state.selectedNetwork === false ? <div className={"card-footer "}>
                                                <div className={"form-row row d-flex justify-content-around"}></div>
                                            </div> :
                                            (this.state.selectedMusic === false) ?
                                                <div className={"card-footer "}>
                                                    <div className={"form-row row d-flex justify-content-around"}>
                                                        <div className="float btn btn-success  col-md-4 footer"
                                                             onClick={(e) => {
                                                                 this.state.selectedNetwork === false ? this.setState({selectedNetwork: true}) : this.setState({selectedMusic: true})
                                                             }
                                                             }>
                                                            NEXT
                                                        </div>
                                                    </div>
                                                </div> : <div className={"card-footer "}>
                                                    <div className={"form-row row d-flex justify-content-around"}>
                                                        <Button type="submit"
                                                                className="float btn btn-success  col-md-4 footer"
                                                                disabled={isSubmitting}>
                                                            GENERATE VIDEOCLIP
                                                        </Button>
                                                    </div>
                                                </div>
                                    }
                                </div>
                            </form>
                        </div>

                    )}
                </Formik>
                <div className={"row"}>
                    {this.state.requestMade === true ?
                        this.state.videoURL.length === 0 ?
                            <div className={"d-flex justify-content-center"}>
                                <Spinner animation="border"/>
                            </div> :
                            <iframe src={"http://localhost:8001/display/" + this.state.videoURL}
                                    frameBorder="0"
                                    style={{height: this.state.videoURLRes}}/> :
                        <div/>
                    }

                </div>
            </div>
        )
    }
}

const myFormSchema = yup.object().shape({});
export default DemoPage;
